@import url(//fonts.googleapis.com/earlyaccess/cwtexfangsong.css);

h1.logo {
    font-family: 'cwTeXFangSong', serif;
    text-align: center;
    font-size: 55px;
}

#number {
    font-family: 'cwTeXFangSong', serif;
    text-align: center;
    font-size: 25px;
    font-style: italic;
}

#description {
    font-family: 'cwTeXFangSong', serif;
    text-align: center;
    font-size: 25px;
}

.result {
    text-align:center;
}


.isa_info, .isa_success, .isa_warning, .isa_error {
	margin: 10px 0px;
	padding:12px;
}
.isa_info {
    color: #00529B;
    background-color: #BDE5F8;
}
.isa_success {
    color: #4F8A10;
    background-color: #DFF2BF;
}
.isa_warning {
    color: #9F6000;
    background-color: #FEEFB3;
}
.isa_error {
    color: #D8000C;
    background-color: #FFBABA;
}
.isa_info i, .isa_success i, .isa_warning i, .isa_error i {
    margin:10px 22px;
    font-size:2em;
    vertical-align:middle;
}